  const [charCount_Briefstep3, setcharCount_Briefstep3] = useState(0);
  const [charCount_Problemstep3, setcharCount_Problemstep3] = useState(0);
  const [charCount_Solutionstep3, setcharCount_Solutionstep3] = useState(0);
